.faq {
    background-color: $ghost-white;
}

.faq__content-wrapper {
    max-width: 1366px;
    margin-left: auto;
    margin-right: auto;
    padding: 123px 103px 120px;

    @media (max-width: $tablet-width) {
        padding: 100px 45px 93px;
    }

    @media (max-width: $mobile-width) {
        padding: 70px 15px;
    }
}

.faq__title {
    font-family: 'Alumni Sans', sans-serif;
    font-size: 54px;
    letter-spacing: 1.62px;
    font-weight: 600;
    line-height: normal;
    margin: 0 0 40px;

    @media (max-width: $tablet-width) {
        text-align: center;
        font-size: 42px;
        letter-spacing: 1.26px;
    }

    @media (max-width: $mobile-width) {
        font-size: 32px;
        letter-spacing: 0.96px;
        margin-bottom: 29px;
    }
}

.faq__tabs {
    display: flex;

    @media (max-width: $tablet-width) {
        flex-direction: column;
    }
}

.faq__tabs-list {
    display: flex;
    flex-direction: column;
    margin-right: 40px;

    @media (max-width: $tablet-width) {
        flex-direction: row;
        margin-right: 0;
        margin-bottom: 40px;
    }

    @media (max-width: $mobile-width) {
        flex-wrap: wrap;
        margin-bottom: 22px;
    }
}

.faq__tabs-button {
    font-family: "Roboto Condensed",sans-serif;
    font-size: 18px;
    line-height: 18px;
    font-weight: 500;
    text-align: center;
    width: 260px;
    padding: 17px 0;
    margin-bottom: 10px;
    background-color: $color-default-white;
    border: 1px solid rgba(16, 38, 84, 0.20);
    cursor: pointer;

    &:hover,
    &:focus {
        border-color: $Sapphire;
    }

    &.is-active {
        color: $color-default-white;
        background: var(--1, linear-gradient(151deg, #ED0233 0%, #003EB7 100%));background: var(--1, linear-gradient(151deg, #ED0233 0%, #003EB7 100%));
        border-color: $color-transparent;
    }

    &:disabled {
        color: #C8C9CC;
        border-color: rgba(16, 38, 84, 0.20);
    }

    @media (max-width: $tablet-width) {
        width: 162px;
        margin-bottom: 0;
        margin-right: 10px;

        &:last-of-type {
            margin-right: 0;
        }
    }

    @media (max-width: $mobile-width) {
        font-size: 16px;
        line-height: 16px;
        width: 141px;
        padding: 13px 0;
        margin: 0 0 8px 0;
        
        &:nth-child(2n-1) {
            margin-right: 8px;
        }
    }
}

.tabs__content {
    flex-grow: 1;
}

.faq__accordion-element {
    border-top: 1px solid rgba(28, 51, 116, 0.20);

    &:last-of-type {
        border-bottom: 1px solid rgba(28, 51, 116, 0.20);
    }
}

.faq__accordion-button {
    position: relative;
    font-family: "Roboto Condensed",sans-serif;
    font-size: 24px;
    font-weight: 500;
    line-height: 29px;
    width: 100%;
    padding: 31px 0 31px;
    color: $Sapphire;
    background-color: $color-transparent;
    text-align: left;
    
    .faq__accordion-element.is-active &{
        padding-bottom: 14px;
    }

    @media (max-width: $tablet-width) {
        padding: 30px 44px 30px 0;
    }

    @media (max-width: $mobile-width) {
        font-size: 18px;
        line-height: 21px;
        padding: 21px 68px 20px 0;
    }
}

.faq__accordion-button-circle {
    position: absolute;
    // top: 24px;
    top: 29%;
    right: 1px;
    width: 42px;
    height: 42px;
    border: 1px solid $resolution-blue;
    border-radius: 20px;

    &::before {
        position: absolute;
        width: 1px;
        height: 18px;
        top: 11px;
        right: 19px;
        background-color: $resolution-blue;
        content: "";
    }

    &::after {
        position: absolute;
        width: 1px;
        height: 18px;
        top: 11px;
        right: 19px;
        background-color: $resolution-blue;
        transform: rotate(90deg);
        content: "";
    }

    @media (max-width: $mobile-width) {
        top: 35%;
        right: 0px;
        width: 32px;
        height: 32px;

        &::after {
            height: 11px;
            top: 10px;
            right: 14px;
        }

        &::before {
            height: 11px;
            top: 10px;
            right: 14px;
        }
    }
}

.faq__accordion-element.is-active .faq__accordion-button-circle {
    background-color: $resolution-blue;

    &::after {
         background-color: $color-default-white;
    }
 
    &::before {
        display: none;
    }
}

.faq__accordion-description {
    font-size: 18px;
    font-weight: 300;
    line-height: 25px;
    margin: 0 0 25px 0;

    @media (max-width: $tablet-width) {
        padding-right: 121px;
    }

    @media (max-width: $mobile-width) {
        font-size: 16px;
        line-height: 22.4px;
        padding-right: 21px;
        margin-bottom: 18px;
    }
}
